<template>
  <!--login form-->
  <div class="login-box border">
    <h1 class="pagename">LOGIN</h1>
   <form @submit="loginpls" >
  <div class="mb-3">
    <label>Email address</label>
    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" v-model="tempmail" required>
    <div id="emailHelp" class="form-text">We'll never share your email with anyone else.</div>
  </div>
  <div class="mb-3">
    <label>Password</label>
    <input type="password" class="form-control" id="exampleInputPassword1" v-model="temppass" required>
    <div class="alert alert-danger my-2 py-1" role="alert" v-if="error">
      {{error}}
    </div>
  </div>
  <button type="submit" class="btn btn-primary btn-lg" >Login</button>
  
</form>
</div>
</template>

<script>


export default {
  name: 'Login',
  data(){
    return{
      email:'mysms@gmail.com',
      password:'password',
      tempmail:'',
      temppass:'',
      error:''
    }
  },
  beforeCreate(){
        if (sessionStorage.getItem("token") === null || sessionStorage.getItem("token") !== 'secretpass') {
      console.log('LOGIN!')
    }else{
      this.$router.push('/home')
    }
  },

  methods:{
    loginpls(){
      event.preventDefault();
      if(this.tempmail == this.email){
        console.log('email correct')
        if(this.temppass == this.password){
          console.log('correct pass')
          this.error=''
          sessionStorage.setItem('token', 'secretpass')
          this.$router.push('/home')
        }else{
          this.error='Wrong Password'
          console.log('wrong pass')
        }
      }else{
        this.error = 'wrong Email'
        console.log('wrong email')
      }
    }
  }
}
</script>
